.app-wrapper {
  max-width: 960px;
  margin: 0 auto;
  padding: 10px;
  display: grid;
  grid-template-areas:
    'header header'
    'navbar content';
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 4fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: content;
  position: relative;
}